import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as CryptoJS from 'crypto-js';

const url = 'http://192.168.1.3:8888/';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{


    constructor (private http: HttpClient) {

    }

    password: string = '';

    open(): void {
        localStorage.pwd = this.password;
        this.http.get<Nonce>(url + 'nonce').subscribe(nDat => {
            const hash: string = CryptoJS.SHA3(this.password + nDat.nonce).toString();
            console.log(hash);
            const body = { key: hash }
            console.log(body);
            this.http.post(url + 'door', body).subscribe(dDat => {
                console.log('success');
            }, dErr => {
                console.log('door error');
                console.log(dErr);
            });
        }, nErr => {
            console.log('nonce error');
            console.log(nErr);
        });
    }

    ngOnInit() {
        try { this.password = localStorage.pwd; }
        catch (e) { this.password = ''; }
    }
}

class Nonce {
    nonce: string;
}



